






























































































import Vue from "vue";
import Component from "vue-class-component";
import { AppService } from "../../service/app.service";

import UserAvatar from "@/x-vue/components/user/UserAvatar.vue";
import UserPointProgressBar from "@/x-vue/components/user/UserPointProgressBar.vue";

@Component({
  components: {
    UserAvatar,
    UserPointProgressBar,
  },
})
export default class App extends Vue {
  app = AppService.instance;

  logout(): void {
    this.app.logout();
    this.app.open("/");
  }

  openCafe(id: string): void {
    location.href = "https://" + id + ":" + location.port + "/cafe/admin/edit";
  }
}
