









































import Vue from "vue";
import Component from "vue-class-component";
import router from "@/router";

import AppHeader from "@/components/header/AppHeader.vue";
import AppFooter from "@/components/footer/AppFooter.vue";
import DesktopLeftSidebar from "@/components/sidebar/DesktopLeftSidebar.vue";
import DesktopRightSidebar from "@/components/sidebar/DesktopRightSidebar.vue";
import { AppService } from "./service/app.service";
import { ApiService } from "./x-vue/services/api.service";
import store from "./store";

@Component({
  components: {
    AppHeader,
    AppFooter,
    DesktopLeftSidebar,
    DesktopRightSidebar,
  },
})
export default class App extends Vue {
  app = AppService.instance;
  api = ApiService.instance;

  get contentClass(): string[] {
    const classArrary = []; // [this.$store.state.leftSidebar ? "col-lg-6" : "col-lg-9"];
    if (this.$store.state.leftSidebar) {
      classArrary.push("col-lg-7");
      classArrary.push("px-md-3");
    } else {
      classArrary.push("col-lg-9");
      classArrary.push("pl-md-0");
      classArrary.push("pr-md-3");
    }
    return classArrary;
  }

  async mounted(): Promise<void> {
    // console.log("App::mounted. running mode: ", process.env.NODE_ENV);
    // this.app.portal();

    // will be called only once. for landing.
    this.loadBanners();

    router.afterEach((to) => {
      store.state.currentCategory = to.params.categoryId ? to.params.categoryId : "";

      // Move to top on every page changes.
      window.scrollTo(0, 0);

      // will only be called on navigatin / page changed.
      this.loadBanners();

      // console.log("to.name", to.name);
      if (
        to.name === "Home" ||
        to.name?.indexOf("Forum") === 0 ||
        to.name?.indexOf("Search") === 0 ||
        to.path.indexOf("advertisement") > -1 ||
        to.path.indexOf("admin") > -1
      ) {
        store.commit("leftSidebar", false);
      } else {
        store.commit("leftSidebar", true);
        // console.log("this.app.showLeftSidebar();");
      }
    });
  }

  async loadBanners(): Promise<void> {
    this.app.loadBanners("top");
    this.app.loadBanners("sidebar");
  }
}
