


































































































import Vue from "vue";
import Component from "vue-class-component";
import { AppService } from "@/service/app.service";

import { ApiService } from "@/x-vue/services/api.service";
import { PostModel } from "@/x-vue/interfaces/forum.interface";

@Component({
  components: {},
})
export default class extends Vue {
  app = AppService.instance;
  api = ApiService.instance;

  news: PostModel[] = [];
  qna: PostModel[] = [];
  discussion: PostModel[] = [];
  buyandsell: PostModel[] = [];
  travel: PostModel[] = [];
  latest: PostModel[] = [];

  mounted(): void {
    this.api
      .latestPosts(
        { categoryId: "news", limit: 3, files: "Y" },
        {
          callback: (posts) => (this.news = posts),
        }
      )
      .then((posts) => (this.news = posts))
      .catch((e) => this.app.error(e));
    this.api
      .latestPosts(
        { categoryId: "qna", limit: 5 },
        {
          callback: (posts) => (this.qna = posts),
        }
      )
      .then((posts) => (this.qna = posts))
      .catch((e) => this.app.error(e));
    this.api
      .latestPosts(
        { categoryId: "discussion", limit: 5 },
        {
          callback: (posts) => (this.discussion = posts),
        }
      )
      .then((posts) => (this.discussion = posts))
      .catch((e) => this.app.error(e));
    this.api
      .latestPosts(
        { categoryId: "travel", limit: 1, files: "Y" },
        {
          callback: (posts) => (this.travel = posts),
        }
      )
      .then((posts) => (this.travel = posts))
      .catch((e) => this.app.error(e));
    this.api
      .latestPosts(
        { categoryId: "buyandsell", limit: 3, files: "Y" },
        {
          callback: (posts) => (this.buyandsell = posts),
        }
      )
      .then((posts) => (this.buyandsell = posts))
      .catch((e) => this.app.error(e));

    ApiService.instance
      .latestPosts(
        {
          ids: "qna,discussion,buyandsell,travel,job,greetings",
          limit: 11,
          files: "Y",
          within: 60 * 60 * 24 * 60, // 이 시간 사이, 최근 두 달 글
          order: "noOfComments DESC, createdAt DESC",
          by: "",
        },
        { callback: (posts) => (this.latest = posts) }
      )
      .then((posts) => (this.latest = posts));
  }
}
