import Vue from "vue";
import Vuex from "vuex";

// import apiStore from "@/x-vue/services/api.store";
import VueRouter from "vue-router";
import { AdvertisementSettings, CafeModel, CafeSettings, UserModel } from "@/x-vue/interfaces/interfaces";
import { PostModel } from "@/x-vue/interfaces/forum.interface";
import { Banner, AllCategoryBanners } from "@/x-vue/interfaces/advertisement.interface";
import { advKey } from "@/service/functions";

Vue.use(Vuex);

export interface AppState {
  user: UserModel;
  router: VueRouter;
  posts: PostModel[];
  // the post on current view page.
  post: PostModel;
  // the category of current page. It will be updated on list, view, edit page.
  // Useage - When currencyCategory changes, the app can show difference banner for that category.
  currentCategory: string;
  // This becomes true if the post is being loaded.
  postLoading: boolean;
  banners: AllCategoryBanners;

  // Global advertisement settings;
  advertisementSettings: AdvertisementSettings;

  // redraw the main content if it needs to refresh the content base on params/query
  contentViewKey: number;

  // To show left sidebar or not on desktop.
  leftSidebar: boolean;

  // cafe settings
  isRootCafe: boolean;
  isSubCafe: boolean;
  // becomes true if the cafe has subcafe domain and is not exists.
  cafeNotExist: boolean;
  currentCafe: CafeModel;
  myCafeList: CafeModel[];
  // Global cafe settings
  cafeSettings: CafeSettings;
}

// Add your state here !
const state: AppState = {
  user: new UserModel(),
  router: {} as VueRouter,
  posts: [],
  post: new PostModel(),
  currentCategory: "",
  postLoading: false,
  banners: {},
  advertisementSettings: {} as AdvertisementSettings,

  contentViewKey: 1,

  leftSidebar: false,

  isRootCafe: true,
  isSubCafe: false,
  cafeNotExist: false,
  currentCafe: new CafeModel(),
  myCafeList: [],
  cafeSettings: {} as CafeSettings,
};

export default new Vuex.Store({
  state: state,
  mutations: {
    user: (state, user) => {
      state.user = user;
    },
    cafeSettings: (state, cafeSettings) => {
      state.cafeSettings = cafeSettings;
    },
    myCafeList: (state, myCafeList) => {
      state.myCafeList = myCafeList;
    },
    currentCafe: (state, currentCafe) => {
      state.currentCafe = currentCafe;
    },
    currentCategory: (state, currentCategory) => {
      state.currentCategory = currentCategory;
    },
    banners: (state, opt: { bannerType: string; category: string; banners: Banner[] }) => {
      const k = advKey(opt.bannerType, opt.category); // `${opt.bannerType}-${opt.category}`;

      const data: AllCategoryBanners = { [k]: opt.banners };
      // state 를 Object.assign() 할 때, 3차원 연관 배열이면, 안된다. Vue.js 형식은 모든 요소를 다 일일히 reactive 하게 해야하는데, 3차원 배열이면 안되는 것 같다.
      state.banners = Object.assign({}, state.banners, data);
      console.log(state.banners);
    },
    contentViewKey: (state) => state.contentViewKey++,
    isRootCafe: (state) => {
      state.isRootCafe = true;
      state.isSubCafe = false;
    },
    isSubCafe: (state) => {
      state.isRootCafe = false;
      state.isSubCafe = true;
    },
    cafeNotExist: (state) => {
      state.cafeNotExist = true;
    },
    leftSidebar: (state, v) => {
      state.leftSidebar = v;
    },
    advertisementSettings: (state, v) => {
      state.advertisementSettings = v;
    },
  },
  actions: {},
  modules: {},
});
