













































































import Vue from "vue";
import Component from "vue-class-component";
import AdvertisementTopBanner from "@/x-vue/widgets/advertisement/AdvertisementTopBanner.vue";
import { AppService } from "@/service/app.service";
import SearchBox from "./SearchBox.vue";
import DesktopMainMenuLeft from "@/components/header/DesktopMainMenuLeft.vue";
import DesktopMainMenuRight from "@/components/header/DesktopMainMenuRight.vue";

@Component({
  components: {
    AdvertisementTopBanner,
    SearchBox,
    DesktopMainMenuLeft,
    DesktopMainMenuRight,
  },
})
export default class extends Vue {
  app = AppService.instance;
  private keyword = "";
  get title(): string {
    return this.app.cafeName;
    // return this.app.api.currentCafeSettings()?.name ?? "Site name";
  }
  get logo(): string {
    return this.app.cafeLogo;
  }
  search(): void {
    console.log("search: keyword: ", this.keyword);
  }
}
