































































































































































import Vue from "vue";
import { Component, Watch } from "vue-property-decorator";
import SearchBox from "./SearchBox.vue";
import MenuSvg from "@/x-vue/svg/MenuSvg.vue";
import CloseSvg from "@/x-vue/svg/CloseSvg.vue";
import { AppService } from "@/service/app.service";
import UserAvatar from "@/x-vue/components/user/UserAvatar.vue";
@Component({
  components: { SearchBox, MenuSvg, UserAvatar, CloseSvg },
})
export default class extends Vue {
  app = AppService.instance;

  open = false;
  @Watch("$route")
  onRouteChange(): void {
    if (this.open) this.open = false;
  }
}
