Vue.use(LayoutPlugin);

// Vue.use(CarouselPlugin);

// Optionally install the BootstrapVue icon components plugin
// Vue.use(IconsPlugin);

const x = XHelper.instance;

ApiService.instance.init({
  serverUrl: config.apiServerUrl(),
  userChanges: (user: UserModel) => {
    // User login or logout. Or profile update.
    store.commit("user", user);

    // @todo test needed.
    // Save firebase token to backend when user visit as logged in, login, register, profile update.
    if (user.loggedIn) {
      if (MessagingService.instance.token) {
        ApiService.instance.saveToken(MessagingService.instance.token, location.hostname);
      }
    }
  },
  texts: texts,
});

const app = AppService.instance;

Vue.prototype.$app = app;

const vm = new Vue({
  router,
  store,
  data: {
    a: "apple",
    landing: true,
  },
  render: (h) => h(App),
  beforeCreate: function () {
    ``;
  },
  mounted: async function () {
    ``;
  },
  methods: {
    aTag: function (href: string) {
      this.$router.push(href).catch(() => null);
    },
    changeLanguage: function (v: string) {
      app.setCookie("language", v);
      location.reload();
    },
  },
}).$mount("#app");

store.state.router = router;
x.init({ vm: vm });

ComponentService.instance.init({
  alert: (t: string, m: string) => x.alert(t, m),
  confirmToast: (options: ConfirmToast) => x.confirmToast(options),
  toast: (options: Toast) => x.toast(options),
  confirm: (t: string, m: string) => x.confirm(t, m),
  promptToast: (options: PromptToast) => x.promptToast(options),
});

/// firebase
app.initFirebase();

