













































































































import Vue from "vue";
import { Component } from "vue-property-decorator";
import TwoPhotosLatestTexts from "@/x-vue/widgets/post/TwoPhotosLatestTexts.vue";
import BigPhoto from "@/x-vue/widgets/post/BigPhoto.vue";
import TextThumbnail from "@/x-vue/widgets/post/TextThumbnail.vue";
import LatestPhotos from "@/views/home/desktop/LatestPhotos.vue";
import LatestHot from "@/views/home/desktop/LatestHot.vue";
import TwoColumnStoryGroupB from "@/x-vue/widgets/post/TwoColumnStoryGroupB.vue";
import TwoPhotoTopThumbnailsWithTextAndPhotoBottom from "@/x-vue/widgets/post/TwoPhotoTopThumbnailsWithTextAndPhotoBottom.vue";
import LeftBigPhotoRightSmallPhotosAndTexts from "@/x-vue/widgets/post/LeftBigPhotoRightSmallPhotosAndTexts.vue";
import { AppService } from "@/service/app.service";
import MetroThumbnails from "@/x-vue/widgets/post/MetroThumbnails.vue";

@Component({
  components: {
    TwoPhotosLatestTexts,
    BigPhoto,
    TextThumbnail,
    LatestPhotos,
    LatestHot,
    TwoColumnStoryGroupB,
    TwoPhotoTopThumbnailsWithTextAndPhotoBottom,
    LeftBigPhotoRightSmallPhotosAndTexts,
    MetroThumbnails,
  },
})
export default class extends Vue {
  app = AppService.instance;
  tabIndex = -1;
  mounted(): void {
    // 마지막 선택된 탭 보여주기
    const i = this.app.getCookie("homeTabIndex");
    if (i) {
      this.$nextTick(() => {
        this.tabIndex = parseInt(i);
      });
    }
  }
  // 마지막 선택된 탭 저장
  tabChanged(currentTabs: number): void {
    this.app.setCookie("homeTabIndex", currentTabs.toString());
  }
}
