  initFirebase(): void {
    this.firebaseApp = initializeApp({
      apiKey: "AIzaSyDWiVaWIIrAsEP-eHq6bFBY09HLyHHQW2U",
      authDomain: "sonub-version-2020.firebaseapp.com",
      databaseURL: "https://sonub-version-2020.firebaseio.com",
      projectId: "sonub-version-2020",
      storageBucket: "sonub-version-2020.appspot.com",
      messagingSenderId: "446424199137",
      appId: "1:446424199137:web:f421c562ba0a35ac89aca0",
      measurementId: "G-F86L9641ZQ",
    });

    this.auth = getAuth(this.firebaseApp);

    this.chat = ChatService.instance;

    MessagingService.instance.init({
      onMessage: (payload: MessagePayload) => {
        const notification = payload.notification;
        const data = payload.data ? payload.data : {};
        let url = "";

        // check if the sender is current loggedIn user
        if (store.state.user.idx == parseInt(data["senderIdx"])) return;

        /**
         * If the type is post then move it to a specific post.
         */
        if (data["type"] === "post") {
          url = "/" + data["idx"];
        }

        /**
         * If the type is chat then move it to private chat.
         */
        if (data["type"] === "chat") {
          url = "/chat-message?roomId=" + data["roomId"];
          // if user is inside the room return
          if (store.state.router.currentRoute.fullPath == url) {
            return;
          }
        }

        // show toast
        ComponentService.instance.toast({
          title: notification?.title || "",
          message: notification?.body || "",
          placement: PLACEMENT.BOTTOM_RIGHT,
          hideDelay: 10000,
          clickCallback: () => {
            const path = store.state.router.currentRoute.path;
            if (path == url || (data["type"] === "chat" && path == "/chat-message")) {
              store.commit("contentViewKey");
            }
            store.state.router.push(url);
          },
        });
      },
    });
    /**
     * Firebase user login or logout, change
     */
    onAuthStateChanged(this.auth, (user) => {
      if (user) {
        console.log("firebase user logged in; ");
        console.log("firebase user email;", user?.email);
        console.log("firebase user displayName;", user?.displayName);
        if (!this.user.firebaseUid) {
          // Firebase UID 업데이트
          //
          // 소셜 로그인을 하면, 자동으로 UID 를 서버에 저장하는데,
          // 이메일/비밀번로 로그인 등에서는 Firebase UID 를 바로 저장하지 않는다.
          // 이와 같은 때, 여기서 사용자의 Firebase UID 를 백엔드에 저장해 준다.
          // If user has no firebaseUid on backend, then updated it.
          console.log("If user has no firebaseUid on backend, then updated it;");
          this.api.userUpdate({ firebaseUid: user.uid });
        }
      } else {
        console.log("firebase user logged out;", this.loggedIn, this.user, ApiService.instance.user.loggedIn);
        setTimeout(() => {
          console.log("firebase user logged out;", this.loggedIn, this.user, ApiService.instance.user.loggedIn);
        }, 1000);
        if (this.loggedIn) {
          console.log("but the user logged in, so trying to login to firebase;");
          this.loginOrRegisterIntoFirebase();
        }
        // console.log("user ", this.loggedIn ? "logged in" : "logged out");
      }
    });
  }

