  /**
   * handle and shows error.
   *
   * `cafe_not_exists` 와 같이 크게 중요하지 않은 에러는 화면에 표시를 하지 않는다.
   *
   * @param e error code
   *
   * @return true when the alert(error) modal has closed.
   */
  // eslint-disable-next-line
  async error(e: any): Promise<void> {
    console.error("e: ", typeof e, e);
    if (typeof e === "string" && e.indexOf("error_") === 0) {
      // Matrix backend error
      // 백엔드 에러
      if (
        e === Err.wrong_session_id ||
        e == Err.cafe_not_exists ||
        e == Err.category_not_exists ||
        e == Err.entity_not_found ||
        e == Err.user_not_found_by_that_session_id
      ) {
        // 무시할 에러
        console.error(e);
      } else {
        return this.x.error(e);
      }
    } else if (e && typeof e.code !== "undefined") {
      // firebase error and may be others.
      console.error(e);
      return this.alert("error", e.code);
    } else if (e && typeof e.message !== "undefined") {
      // firebase error and may be others.
      console.error(e.message);
      return this.alert("error", e.message);
    } else {
      return this.alert("error", e);
    }
  }

