






import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { Banner } from "@/x-vue/interfaces/advertisement.interface";
import { AdvertisementService } from "@/x-vue/services/advertisement.service";

@Component({})
export default class extends Vue {
  @Prop({ default: "right" }) position!: string;
  @Prop({ default: () => [] }) banners!: Banner[];
  @Prop({ default: 9000 }) rotationInterval!: number;

  index = 0;

  mounted(): void {
    this.rotate();
  }

  get _banners(): Banner[] {
    return this.banners.filter((v, i) => {
      if (this.position == "right") return i % 2 == 0;
      else return i % 2 != 0;
    });
  }

  get currentBanner(): Banner {
    if (!this._banners.length) {
      return { bannerUrl: this.defaultUrl };
    }
    return this._banners[this.index % this._banners.length];
  }

  /**
   * @see readme.md of materix.
   */
  get defaultUrl(): string {
    return `/tmp/${this.position}-banner.jpg`;
  }

  rotate(): void {
    setInterval(() => this.index++, this.rotationInterval);
  }

  onClick(): void {
    AdvertisementService.instance.openAdvertisement(this.currentBanner);
  }
}
