










import { PostModel } from "@/x-vue/interfaces/forum.interface";
import { ApiService } from "@/x-vue/services/api.service";
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import PhotoTextUser from "@/x-vue/widgets/post/PhotoTextUser.vue";

@Component({
  components: { PhotoTextUser },
})
export default class extends Vue {
  @Prop() ids!: string;
  @Prop({ default: "" }) subcategory!: string;
  posts1: PostModel[] = [];
  posts2: PostModel[] = [];

  mounted(): void {
    ApiService.instance
      .latestPosts(
        {
          ids: this.ids,
          subcategory: this.subcategory,
          limit: 4,
          files: "Y",
          within: 60 * 60 * 24 * 7, // 최근 7일 (1주) 동안의 최신 글에서 코멘트 많은 순으로 출력.
          order: "noOfComments DESC, createdAt DESC",
          by: "",
        },
        { callback: (posts) => (this.posts1 = posts) }
      )
      .then((posts) => (this.posts1 = posts));

    ApiService.instance
      .latestPosts(
        {
          ids: this.ids,
          subcategory: this.subcategory,
          limit: 4,
          files: "Y",
          betweenFrom: 60 * 60 * 1, // 이 시간 시작 부터,
          betweenTo: 60 * 60 * 24 * 60, // 이 시간 사이, 최근 두 달 글
          order: "noOfComments DESC, createdAt DESC",
          by: "",
        },
        { callback: (posts) => (this.posts2 = posts) }
      )
      .then((posts) => (this.posts2 = posts));
  }
}
