




























































import Vue from "vue";
import Component from "vue-class-component";
import { AppService } from "../../service/app.service";
import AdvertisementSidebarBanner from "@/x-vue/widgets/advertisement/AdvertisementSidebarBanner.vue";
import OpenWeatherMap from "@/x-vue/components/weather/OpenWeatherMap.vue";
import CurrencyConverter from "@/x-vue/components/currency-converter/CurrencyConverter.vue";
import SideLoginLogout from "@/components/user/SideLoginLogout.vue";
import LatestPostsBy from "@/x-vue/widgets/post/LatestPostsBy.vue";
import { ApiService } from "@/x-vue/services/api.service";
import { UserStats } from "@/x-vue/interfaces/interfaces";

@Component({
  components: {
    AdvertisementSidebarBanner,
    OpenWeatherMap,
    CurrencyConverter,
    SideLoginLogout,
    LatestPostsBy,
  },
})
export default class App extends Vue {
  app = AppService.instance;
  api = ApiService.instance;

  stats: UserStats = {} as UserStats;

  async mounted(): Promise<void> {
    try {
      this.stats = await this.api.userStats();
      // console.log("status; ", this.stats);
    } catch (e) {
      this.app.error(e);
    }
  }
}
