





























































































































import Vue from "vue";
import Component from "vue-class-component";
import { AppService } from "@/service/app.service";

import { PostModel } from "@/x-vue/interfaces/forum.interface";
import { ApiService } from "@/x-vue/services/api.service";
import UserAvatar from "@/x-vue/components/user/UserAvatar.vue";
import { UserModel } from "@/x-vue/interfaces/interfaces";

@Component({ components: { UserAvatar } })
export default class Home extends Vue {
  app = AppService.instance;

  greetings: PostModel[] = [];
  latestUsers: UserModel[] = [];
  async mounted(): Promise<void> {
    ApiService.instance
      .latestPosts(
        {
          categoryId: "greetings",
          limit: 5,
        },
        { callback: (posts) => (this.greetings = posts) }
      )
      .then((posts) => (this.greetings = posts));

    try {
      this.latestUsers = await ApiService.instance.latestUserByProfilePhoto(9);
    } catch (e) {
      this.app.error(e);
    }
  }
}
